.public-banner {
  height: 200px; }
  @media screen and (min-width: 48em) {
    .public-banner {
      height: 450px; } }
  .public-banner__default, .public-banner__custom {
    background-size: cover;
    width: 100%;
    height: 100%; }
  .public-banner__default {
    background-image: url("../../img/site/banners/main.png");
    background-repeat: no-repeat;
    background-position: center; }
  .public-banner__text {
    color: #fefefe;
    font-family: "MuseoRegular", sans-serif;
    font-weight: normal;
    max-width: 400px;
    margin: 0 auto; }
    @media screen and (min-width: 48em) {
      .public-banner__text {
        max-width: 750px; } }
    .public-banner__text-number {
      font-size: 3rem;
      display: inline-block;
      width: 18%;
      margin: 0;
      height: 100px; }
      @media screen and (min-width: 48em) {
        .public-banner__text-number {
          font-size: 5rem; } }
    .public-banner__text-title {
      font-size: 1.3rem;
      display: inline-block;
      width: 80%;
      line-height: 1;
      height: 100px;
      vertical-align: top;
      padding-top: 14px; }
      @media screen and (min-width: 48em) {
        .public-banner__text-title {
          padding-top: 31px;
          font-size: 2rem; } }
    .public-banner__text-button {
      display: block;
      width: 30%;
      margin-left: 18%;
      margin-left: calc(18% + 5px); }
      @media screen and (max-width: 47.9375em) {
        .public-banner__text-button {
          display: none !important; } }
